import Vue from 'vue'
import Router from 'vue-router'
import store from '../store'
Vue.use(Router)

export const constantRouterMap = [
    {
      path: '/login',
      name: '登录页面',
      component: () => import('@/view/login/login')
    },
    {
      path: '/404',
      name: '登录页面',
      component: () => import('@/view/err/404')
    },
    {
      path: '/changePass',
      name: '修改密码',
      component: () => import('@/view/user/change_password'),
      meta: { title: "修改密码" },
    }
  ]
//实例化vue的时候只挂载constantRouter
export default new Router({
  routes: constantRouterMap
});

export const asyncRouterMap = [
  {
    path: '/home',
    name: 'Home',
    component: () => import('@/view/home/home'),
    meta: { role: ['admin'] },
    children:[
      {
        path: '/name',
        name: 'name',
        icon:"el-icon-s-data",
        component: () => import('@/view/layout/layout'),
        meta: { role: ['admin','staff','super_editor'], title:"首页" },
      },
    ]
  },
  {
    path: '/home',
    name: 'Home',
    component: () => import('@/view/home/home'),
    meta: { role: ['admin'] },
    children:[
      {
        path: '/user',
        name: 'user',
        icon:"el-icon-user",
        meta: { role: ['admin','staff','super_editor'], title: "用户列表" },
        component: () => import('@/view/user/user'),
        titles: { title:"用户列表" }
      }
    ]
  },
  {
    path: '/home',
    name: 'Home',
    component: () => import('@/view/home/home'),
    meta: { role: ['admin'] },
    children:[
      {
        path: '/order',
        name: 'order',
        icon:"el-icon-s-order",
        component: () => import('@/view/order/order'),
        meta: { role: ['admin','staff','super_editor'], title: "订单列表" },
      }
    ]
  },
  {
    path: '/home',
    name: 'Home',
    component: () => import('@/view/home/home'),
    meta: { role: ['admin'] },
    children:[
      {
        path: '/banner',
        name: 'banner',
        icon: "el-icon-menu",
        component: () => import('@/view/banner/banner'),
        meta: { role: ['admin','super_editor'], title: "广告信息" },
      }
    ]
  },
  {
    path: '/home',
    name: 'Home',
    component: () => import('@/view/home/home'),
    meta: { role: ['admin'] },
    children:[
      {
        path: '/adminUser',
        name: 'adminUser',
        icon: "el-icon-menu",
        component: () => import('@/view/jurisdiction/user'),
        meta: { role: ['admin','super_editor'], title: "用户列表" },
      }
    ]
  },
  {
    path: '/home',
    name: 'Home',
    component: () => import('@/view/home/home'),
    meta: { role: ['admin'] },
    children:[
      {
        path: '/addAdminUser',
        name: 'addAdminUser',
        icon: 'el-icon-circle-plus-outline',
        component: () => import('@/view/jurisdiction/add_user'),
        meta: { role: ['admin','super_editor'], title: "新增用户" },
      },
      {
        path: '/editor',
        name: 'editor',
        icon: 'el-icon-files',
        component: () => import('@/view/editor/editor'),
        meta: { title: "文章管理" },
      }
    ]
  },
  { path: '*', redirect: '/404', hidden: true }
];













